.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f4f4f4;
  margin: 0;
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: #292f36;
  padding: 40px;
  color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  width: 80%;
  max-width: 800px;
  text-align: center;
}

h1 {
  font-size: 36px;
  margin-bottom: 10px;
  color: #f8c31c;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #e2e1e1;
}

h3 {
  font-size: 20px;
  font-weight: 600;
  margin-top: 30px;
  color: #979797;
}

p {
  font-size: 18px;
  line-height: 1.6;
  color: #666666;
}

.facul p {
  margin-top: 20px;
  font-weight: bold;
}

.progra, .curs {
  margin-top: 30px;
  text-align: left;
}

.progra h3, .curs h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
  color: #f8f8f8;
}

.progra div, .curs div {
  padding: 15px;
  border: 1px solid #dddddd;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  margin-top: 10px;
}

.App-header h2:last-child {
  margin-top: 40px;
}

.navbar {
  background-color: #292f36;
  color: #ffffff;
  padding: 10px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  color: #f8c31c;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin: 0 15px;
}

.nav-links a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #f8c31c;
}